.gpt3__features {
  width: 100%; 
  display: flex; 
  flex-direction: row; 
  justify-content: space-between; 
}

.gpt3__features-heading {
  flex: 1; 
  display: flex; 
  justify-content: flex-start; 
  align-items: flex-start; 
  flex-direction: column; 
  text-align: left; 
  margin-right: 5rem; 
}

.gpt3__features-container{
  flex: 1; 
  display: flex; 
  flex-direction: column;
  gap: 2rem; 
  color: red;
}

.gpt3__features-heading h1 {
  font-family: var(--font-family);
  font-size: 34px;
  font-weight: 800;
  line-height: 45px;
  letter-spacing: 0em;
  text-align: left;
  max-width: 426px; 
}
.gpt3__features-heading p {
  font-family: var(--font-family);
  font-size: 16px;
  font-weight: 500;
  line-height: 30px;
  letter-spacing: 0em;
  text-align: left;
  color: var(--color-subtext);
  margin-top: 2rem; 
}

.gpt3__features-container {
  flex: 1.5; 
  display: flex; 
  flex-direction: column; 
  justify-content: flex-start; 
  align-items: center; 
}

@media screen and (max-width: 990px) {
  .gpt3__features {
    display: flex; 
    flex-direction: column; 
  }

  .gpt3__features-heading {
    margin: 0 0 2rem 0; 
  }
}

@media screen and (max-width: 550px) {
  .gpt3__features {

  }

  .gpt3__features-heading h1 {
    font-size: 28px; 
    line-height: 40px; 
  }


}

