.gpt3__navbar {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 2rem 6rem;
}

.gpt3__navbar-links {
  flex: 1;
  display: flex;
  justify-content: flex-start;
  align-items: center;
}

.gpt3__navbar-links_logo {
  margin-right: 2rem;
}

.gpt3__navbar-links_logo img {
  width: 62.56px;
  height: 16.02px;
}

.gpt3__navbar-links_container {
  display: flex;
  flex-direction: row;
}

.gpt3__navbar-sign-in {
  display: flex;
  justify-content: flex-end;
  align-items: center;
}

.gpt3__navbar-links_container p,
.gpt3__navbar-sign-in p,
.gpt3__navbar-menu_container p {
  color: #ffffff;
  font-family: var(--font-family);
  font-weight: 500;
  font-size: 18px;
  line-height: 25px;
  text-transform: capitalize;
  margin: 0 1rem;
  cursor: pointer;
}

.gpt3__navbar-sign-in button {
  /* width: 152px;
  height: 58px; */
  padding: .5rem 1rem;
  background-color: #FF4820;
  border: none;
  border-radius: 3px;
  font-size: 18px;
  font-weight: 500;
  font-family: Manrope;
  line-height: 25px;
  color: #ffffff;
  cursor: pointer;
  outline: none; 
}

.gpt3__navbar-menu {
  margin-left: 1rem; 
  display: none; 
  position: relative; 
}

.gpt3__navbar-menu svg {
  cursor: pointer; 
}

.gpt3__navbar-menu_container {
  display: flex; 
  justify-content: flex-end; 
  align-items: flex-end; 
  flex-direction: column; 
  text-align: end; 
  background-color: var(--color-footer); 
  padding: 2rem; 
  position: absolute; 
  top: 40px; 
  right: 0; 
  margin-top: 1rem; 
  min-width: 210px; 
  border-radius: 8px; 
  box-shadow: 0 0 8 rgba(0, 0, 0, 0.3); 
}

.gpt3__navbar-links_container-links-sign-in {
  display: none; 
}

.gpt3__navbar-menu_container p {
  margin: 1rem 0; 
}

.gpt3__navbar-menu_container-links-sign-in {
  display: none;
}

.gpt3__navbar-menu_container-links button {
  padding: .5rem 1rem;
  background-color: #FF4820;
  border: none;
  border-radius: 3px;
  font-size: 18px;
  font-weight: 500;
  font-family: Manrope;
  line-height: 25px;
  color: #ffffff;
  cursor: pointer;
  outline: none; 
}


@media only screen and (max-width: 1024px) {
  .gpt3__navbar-links_container {
    display: none; 
  }

  .gpt3__navbar-menu {
    display: flex; 
  }
}

@media only screen and (max-width: 700px) {
  .gpt3__navbar {
    padding: 2rem 4rem; 
  }
}

@media only screen and (max-width: 480px) {
  .gpt3__navbar {
    padding: 2rem; 
  }

  .gpt3__navbar-sign-in {
    display: none; 
  }

  .gpt3__navbar-menu_container {
    top: 20px; 
  }

  .gpt3__navbar-menu_container-links-sign-in {
    display: block;
  }
}